<template>
  <div>
    <button class="button" @click="isModalOpen = true">Add User</button>
    <teleport to="body">
      <Modal v-if="isModalOpen" @close="isModalOpen = false">
        <template #title> Add New User </template>
        <template #body>
          <form class="form" @submit.prevent="submit">
            <div>
              <label for="name">Name</label>
              <input
                type="text"
                placeholder="User Name"
                class="form-input"
                id="name"
                v-model="state.form.name"
              />
            </div>
            <div>
              <label for="email">Email</label>
              <input
                type="email"
                placeholder="User Email"
                class="form-input"
                id="email"
                v-model="state.form.email"
              />
            </div>
            <div>
              <label for="url">Avatar URL</label>
              <input
                type="text"
                placeholder="User Avatar URL"
                class="form-input"
                id="url"
                v-model="state.form.avatar"
              />
            </div>
            <div>
              <input
                type="submit"
                class="form-input form-submit"
                value="Create"
              />
            </div>
          </form>
        </template>
      </Modal>
    </teleport>
  </div>
</template>

<script>
import Modal from "../Modal";
import { ref, reactive } from "vue";
import axios from "../../plugins/axios";
export default {
  components: { Modal },
  setup(props, { emit }) {
    const state = reactive({
      form: {
        name: "",
        email: "",
        avatar: "",
      },
    });
    const isModalOpen = ref(false);

    async function submit() {
      const { data } = await axios.post("/users", state.form);
      emit("new-user-adder", data);
      
      state.form.email = "";
      state.form.name = "";
      state.form.avatar = "";
      isModalOpen.value = false;
    }

    return { isModalOpen, submit, state };
  },
};
</script>

<style scoped>
.form {
  margin-top: 10px;
}
.form-input {
  width: 100%;
  min-height: 30px;
  margin: 7px 0;
  padding: 5px;
  background: #ffffff;
  border: 1px solid #959595;
  box-sizing: border-box;
  border-radius: 9px;
}
.form-submit:hover {
  background-color: #d9d9d9;
}
</style>