<template>
  <section>
    <h1 class="title">Reusable Modal</h1>

    <Modal v-if="isModal1Open" @close='isModal1Open = false'>
        <template #title>
            Modal 1
        </template>
        <template #body>
            <p>This is a body for modal 1</p>
        </template>
    </Modal>
    <button @click="isModal1Open = true" class="button button-reusablemodal">Modal 1</button>

    <Modal v-if="isModal2Open" @close='isModal2Open = false'>
        <template v-slot:title>
            Modal 2
        </template>
        <template v-slot:body>
            <p>This is a body for modal 2</p>
        </template>
    </Modal>
    <button @click="isModal2Open = true" class="button button-reusablemodal">Modal 2</button>
  </section>
</template>

<script>
import Modal from "../components/Modal";
import {ref} from 'vue'
export default {
  components: {Modal},
  setup() {
      const isModal1Open = ref(false);
      const isModal2Open = ref(false);

      return {isModal1Open, isModal2Open}

  }
};
</script>

<style scope>
.title {
  text-align: center;
  margin-top: 20px;
}
.button-reusablemodal {
    display: block;
    margin: 0 auto;
    margin-top: 10px;
}
.red-text {
    color: pink;
}

.blue-text {
    color: rgb(98, 98, 226);
}
</style>