<template>
  <div class="main-block">
    <h1 class="title-block">DC heroes list</h1>
    <p>Heroes : {{ heroesCount }}</p>
    <ul class="list">
      <li v-for="(hero, index) in heroes" :key="index" class="item">
        <div>
        {{ index + 1 }} : {{ hero.name }}
        </div>
        <button @click="removeHero(index)" class="button">X</button>
      </li>
    </ul>
    <form @submit.prevent="addHero" class="item">
      <input type="text" v-model.trim="newHero" ref="newHeroRef" />
      <button type="submit" class="button">Add hero</button>
    </form>
  </div>
</template>

<script>
import { onMounted, ref, computed } from "vue";
export default {
  setup() {
    const isDisabled = ref(true);
    const newHeroRef = ref("");
    const newHero = ref("");
    const heroes = ref([
      { name: "Superman" },
      { name: "Cat woman" },
      { name: "Spiderman" },
    ]);

    onMounted(() => {
      newHeroRef.value.focus();
    });

    const heroesCount = computed({
      get: () => heroes.value.length,
    });

    function removeHero(index) {
      heroes.value.splice(index, 1);
    }
    function addHero() {
      if (!newHero.value == "") {
        heroes.value.push({ name: newHero.value });
        newHero.value = "";
      }
    }

    return {
      heroes,
      newHero,
      removeHero,
      addHero,
      newHeroRef,
      heroesCount,
      isDisabled,
    };
  },
};
</script>

<style scoped>
@import '../style/style.css';
.list {
  margin-top: 20px;
  list-style: none;
  padding: 0;
  padding-left: 20px;
  margin-bottom: 20px;
}
.item {
  display: flex;
  justify-content: space-around;
  margin-bottom: 7px;
}
</style>
