<template>
  <nav>
    <div class="titles">
      <router-link
        v-for="item in list"
        :key="item.to"
        class="title"
        :to="item.to"
        >{{ item.title }}</router-link
      >
      <button v-if="!isLoggedIn" @click="openLogin">Login</button>
      <button v-else @click="logout">Logout</button>
    </div>
  </nav>
</template>

<script>
import firebase from "../utilites/firebase";

export default {
  data() {
    return {
      list: [
        { title: "Heroes list", to: "/heroes" },
        { title: "Calendar", to: "/calendar" },
        { title: "Markdown", to: "/markdown" },
        { title: "Slider", to: "/slider" },
        { title: "Calculator", to: "/calculator" },
        { title: "Modal", to: "/reusable-modal" },
        { title: "Chat", to: "/chat" },
        { title: "User Crud", to: "/user-crud" },
      ],
      
    };
  },
  computed: {
    isLoggedIn(){
      return this.$store.state.isLoggedIn;
    }
  },
  methods: {
    logout() {
      firebase.auth().signOut();
      // .then((res) => {})
      // .catch((e) => {});
    },
    openLogin() {
      this.$store.commit('setLoginModal', true)
    }
  },
};
</script>

<style scoped>
.titles {
  display: block;
  background-color: #642894;
  text-align: center;
  padding: 20px;
  font-size: 24px;
}
a {
  margin: 0 20px;
  color: white;
  text-decoration: none;
  padding: 20px 0;
}
</style>