<template>
  <div class="main-block container">
    <h1 class="title-block">Markdown App</h1>
    <div class="flex">
      <section class="section">
        <textarea
          class="textarea"
          :value="text"
          @input="update"
          ref="markdownTextArea"
        ></textarea>
      </section>
      <section class="section" v-html="markedText"></section>
    </div>
  </div>
</template>

<script>
import marked from "marked";
import useDebounce from "../utilites/composition/useDebounce";
export default {
  data() {
    return {
      text: "",
      debounce: "",
    };
  },
  methods: {
    update(e) {
      const task = () => (this.text = this.text = e.target.value);
      this.debounce(task, 500);
    },
  },
  computed: {
    markedText() {
      return marked(this.text);
    },
  },
  mounted() {
    this.debounce = useDebounce();
    this.$refs.markdownTextArea.focus();
  },
};
</script>

<style scoped>

@import '../style/style.css';
.container {
  width: 700px;
}
.flex {
  display: flex;
}
.section {
  width: 50%;
  height: 100vh;
  border: 1px solid grey;
}
.textarea {
  height: 100%;
  width: 100%;
}
</style>