<template>
  <div v-if="isLoginOpen">
    <section class="background" @click="close"></section>
    <section class="login">
      <div class="flex">
        <div class="loginblock">
          <h1>Login</h1>
          <GoogleLogin @closeLoginFromGoogle="close" />

          <p>Or</p>
          <form class="form" @submit.prevent="submit">
            <div class="form-item">
              <label class="label" for="email">Email or username</label>
              <input
                class="input"
                id="email"
                type="text"
                placeholder="Enter your email or username"
                v-model="form.email"
                ref="emailRef"
              />
            </div>
            <div class="form-item">
              <label for="password">Password</label>
              <input
                class="input"
                id="password"
                type="password"
                placeholder="Enter your password"
                v-model="form.password"
              />
            </div>
            <button class="button" type="submit">
              <span v-if="!isLoading">Login</span>
              <span v-else>:)</span>
            </button>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import firebase from "../utilites/firebase";
import GoogleLogin from "../components/Login/Google";

export default {
  components: { GoogleLogin },
  data() {
    return {
      form: {
        email: "",
        password: "123456",
      },
      isLoading: false,
    };
  },
  computed: {
    isLoginOpen() {
      return this.$store.state.isLoginOpen;
    },
  },
  mounted() {
    // this.$refs.emailRef.focus();
  },
  methods: {
    submit() {
      this.isLoading = true;
      firebase
        .auth()
        .signInWithEmailAndPassword(this.form.email, this.form.password)
        .then((res) => {
          console.log(res);
          this.form.email = "";
          this.form.password = "";
          this.isLoading = false;
          this.close();
        })
        .catch((e) => {
          console.log(e);
          this.isLoading = false;
        });
    },
    close() {
      this.$store.commit('setLoginModal', false)
    },
  },
};
</script>

<style scoped>
.background {
  background-color: grey;
  width: 100%;
  height: 100vh;
  position: absolute;
  opacity: 0.3;
  top: 0;
  z-index: 10;
}
.login {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
.flex {
  display: flex;
  height: 100vh;
}
.loginblock {
  margin: auto;
  z-index: 15;
  background-color: #fff;
  border-radius: 20px;
  padding: 20px;
  width: 300px;
}
.form-item {
  margin-top: 20px;
}
.input {
  height: 39px;
  width: 252px;
  border: 1px solid #263238;
  border-radius: 8px;
  margin-top: 10px;
  padding-left: 5px;
}
.button {
  border: none;
  background: #27ae60;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.16), 0px 2px 4px rgba(0, 0, 0, 0.12),
    0px 1px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  height: 39px;
  width: 252px;
  margin-top: 20px;
  color: #fff;
  font-weight: 500;
}
</style>