<template>
  <div>
    <section class="block">
      <h1 class="title">User Crud</h1>
      <div>
        <Create @newUserAdder="addUser" />
        <table class="table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Avatar</th>
              <th>Name</th>
              <th>Email</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="user in state.users" :key="user.id">
              <td class="table-item">{{ user._id }}</td>
              <td class="table-item">
                <img :src="user.avatar" alt="user.first_name" class="img" />
              </td>
              <td class="table-item">{{ user.name }}</td>
              <td class="table-item">{{ user.email }}</td>
              <td class="table-item">
                <button @click="destroy(user._id)">Delete</button>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="flex-button">
          <button
            class="button button-usercrud"
            @click="prev"
            :disabled="state.users.page === 1"
            :class="state.users.page === 1 ? 'disabled-button' : 'button-abled'"
          >
            Prev
          </button>
          <button
            class="button button-usercrud"
            @click="next"
            :disabled="state.users.page === state.users.total_pages"
            :class="
              state.users.page === state.users.total_pages
                ? 'disabled-button'
                : 'button-abled'
            "
          >
            Next
          </button>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { onMounted, reactive } from "vue";
import axios from "../plugins/axios";
import Create from "../components/UserCrud/Create";
export default {
  components: { Create },
  setup() {
    const state = reactive({
      users: [],
    });
    onMounted(async () => {
      const { data } = await axios.get(`/users`);
      state.users = data;
    });

    async function next() {
      const { data } = await axios.get(`/users?page=2`);
      state.users = data;
    }

    async function prev() {
      const { data } = await axios.get(`/users?page=1`);
      state.users = data;
    }

    async function destroy(id) {
      await axios.delete(`/users/${id}`);
      state.users = state.users.filter((user) => user._id !== id);
    }

    function addUser(data) {
      state.users.push(data);
    }

    return { state, next, prev, destroy, addUser };
  },
};
</script>

<style scope>
.block {
  display: block;
  width: 600px;
  height: 100%;
  margin: 0 auto;
}
.table {
  margin-top: 20px;
  text-align: center;
}
.table-item {
  padding: 7px;
}
.img {
  width: 80px;
  border-radius: 8px;
}
.flex-button {
  display: flex;
  justify-content: space-between;
}
.button-usercrud {
  width: 63px;
  height: 26px;
  background: #ffffff;
  border: 1px solid #959595;
  box-sizing: border-box;
  border-radius: 9px;
}
.button-abled:hover {
  box-shadow: inset 3px 4px 4px rgba(0, 0, 0, 0.25);
}
.disabled-button {
  background: grey;
  box-shadow: none;
}
</style>