<template>
  <div class="main-block slider">
    <h1 class="title-block ">Slider</h1>
    <div class="slide" v-for="(color, index) in sliders" :key="index">
      <transition name="fade">
        <div
          v-if="currentSlide === index"
          class="slide"
          :style="{ backgroundColor: color }"
        ></div>
      </transition>
    </div>
    <div class="dots">
      <div
        v-for="(item, index) in sliders"
        :key="item"
        class="dot"
        :class="currentSlide === index ? 'active_dot' : ''"
        @click="makeActive(index)"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentSlide: 0,
      interval: "",
      sliders: ["green", "yellow", "red"],
      isTitleShowing: true,
    };
  },
  mounted() {
    this.interval = setInterval(() => {
      if (this.currentSlide === 2) {
        this.currentSlide = 0;
      } else {
        this.currentSlide++;
      }
    }, 3000);
  },
  beforeUnmount() {
    clearInterval(this.interval);
  },
  methods: {
      makeActive(index) {
          this.currentSlide = index;
      }
  }
};
</script>

<style scoped>
.slider {
  position: relative;
  width: 100%;
}
.slide {
  position: absolute;
  height: 300px;
  width: 100%;
}
.dots {
  position: absolute;
  top: 350px;
  z-index: 5;
  display: flex;
  justify-content: center;
  left: 50%;
  transform: translateX(-50%);
}
.dot {
  width: 15px;
  height: 15px;
  border-radius: 100%;
  background-color: grey;
  margin: 5px;
  cursor: pointer;
}
.active_dot {
  background-color: #fff;
}

.fade-enter-active,
.fade-leave-active {
  transition: all 1s ease;
}

.fade-enter-from {
  opacity: 0;
  transform: translateX(-100vw);
}
.fade-leave-to {
  opacity: 0.5;
  transform: translateX(100vw);
}
</style>