<template>
  <div class="main-block">
    <h1 class="title-block">Vue Calendar</h1>
    <section class="current">
      <h2>{{ currentMonthName }}</h2>
      <h2>{{ currentYear }}</h2>
    </section>
    <section class="days">
      <p v-for="day in days" :key="day" class="day">{{ day }}</p>
    </section>

    <section class="nums">
      <p v-for="num in startDay()" :key="num" class="num"></p>
      <p
        v-for="num in daysInMonth()"
        :key="num"
        class="num"
        :class="currentDayClass(num)"
      >
        {{ num }}
      </p>
    </section>
    <section class="buttons">
      <button class="button" @click="prev()">Prev</button>
      <button class="button" @click="next()">Next</button>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentDay: new Date().getUTCDate(),
      currentMonth: new Date().getMonth(),
      currentYear: new Date().getFullYear(),
      days: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
    };
  },
  methods: {
    daysInMonth() {
      return new Date(this.currentYear, this.currentMonth + 1, 0).getDate();
    },
    startDay() {
      return new Date(this.currentYear, this.currentMonth, 0).getDay() + 1;
    },
    next() {
      if (this.currentMonth === 11) {
        (this.currentMonth = 0), this.currentYear++;
      } else {
        this.currentMonth++;
      }
    },
    prev() {
      if (this.currentMonth === 0) {
        (this.currentMonth = 11), this.currentYear--;
      } else {
        this.currentMonth--;
      }
    },
    currentDayClass(num) {
        const calendFullDate = new Date(this.currentYear, this.currentMonth, num).toDateString();
        const currentFullDate = new Date().toDateString();
        return calendFullDate === currentFullDate ? 'num_now': ''
    }
  },
  computed: {
    currentMonthName() {
      return new Date(
        this.currentYear,
        this.currentMonth
      ).toLocaleString("default", { month: "long" });
    },
  },
};
</script>

<style scoped>
@import '../style/style.css';
.days,
.nums {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 5px;
  padding: 10px;
}
.nums {
  justify-content: start;
}
.day,
.num {
  display: block;
  padding: 10px;
  width: 14.28%;
  height: 40px;
  text-align: center;
}
.num_now {
  color: darkviolet;
  font-weight: 600;
  border: 1px solid grey;
  border-radius: 100%;
}
.current {
  display: flex;
  justify-content: space-between;
}
h2 {
  margin: 10px;
}
.buttons {
  display: flex;
  justify-content: space-between;
  margin: 10px;
}
.button {
  padding: 5px;
}
</style>
