<template>
  <section>
    <button class="google" @click="google">Login with Google</button>
  </section>
</template>

<script>
import firebase from "../../utilites/firebase"
export default {
    methods: {
        google() {
      var provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(() => {
          this.$emit("closeLoginFromGoogle");
        });
    },
    },
};
</script>

<style scope>
.google {
  display: block;
  margin: 0 auto;
  margin-top: 5px;
}
</style>